import React, { useState,useEffect } from 'react';
import { Menu,P1,P2,P3,P4 } from './';
import { useSetMenu } from './redux/hooks';

export default function Dashboard(props) {
  
  const { menu,setMenu } = useSetMenu();

  useEffect(()=>{
    
    let currentMenu = {
      index: 0,
      title: "中国疫情现有确诊动态图"
    };
    if(window.location.search && window.location.search !== "") {
      let search =  window.location.search.replace("?","");
      let searchArr = search.split('&');
      for(let s of searchArr) {
        let sa = s.split('=');
        if(Array.isArray(sa)) {
          if(sa[0] === "menu") {
              let res = sa[1];
              switch (res) {
                case "1":
                  currentMenu = {index: 1,title: "各省疫情累计确诊动态图"};
                  break;
                case "2":
                  currentMenu = {index: 2,title: "全球疫情现有确诊动态图"};
                  break;
                case "3":
                  currentMenu = {index: 3,title: "国外疫情累计确诊动态图"};
                  break;
                default:
                  break;
              }
              setMenu(currentMenu);
              break;
          }
        }
      }

    }
  },[setMenu])

  const generatePage = ()=> {
    switch (menu.index) {
      case 0: 
        return <P1 />
      case 1:
        return <P2 />
      case 2:
        return <P3 />
      case 3:
        return <P4 />
      default:
        return <P1 />
    }
  }
  
  return (
    <div className="zhangjiang-dashboard">
      <div>
        <div>
          <div> {menu.title}</div>
          <div>
            <img src="./assets/images/logo.png" alt="" />
          </div>
        </div>
        {/* <div>
          <Menu {...props} />
        </div> */}
      </div>
      <div>
        <div style={{display:"flex",justifyContent:"center"}}>
          {
            generatePage()
          }
        </div>
      </div>
    </div>
  );
}

Dashboard.propTypes = {};
Dashboard.defaultProps = {};
