import React, { useState } from 'react';
// import PropTypes from 'prop-types';
import news from "./jsondata/News.json"

export default function RightLayout() {
  const [tabDisplay, setTabDisplay] = useState(true);

  return (
    <div className="right">
      <div className="boxContainer">
        <div className="box ">
          <h2 className="domestic-stock-title right-title">境外机构最新文章展示</h2>
          <div className="right-tab-title">
            <div className={tabDisplay ? "frist frist-select" : "frist frist-unselect"} onClick={() => { setTabDisplay(true) }}>
              热门
              </div>
            <div className={!tabDisplay ? "second second-select" : "second second-unselect"} onClick={() => { setTabDisplay(false) }}>
              推荐
              </div>
          </div>

          <div className="right-tab-div" style={{ display: tabDisplay ? "block" : "none" }}>
            <div></div>
            {/* 组件化一下 */}
            {news.hotnews.map((item,index) => {
              return (
                // 暂用index
                <div key={index}>
                  <div>{item[0].trim()}</div>
                  <div>{item[1].trim()}</div>
                </div>
              )
            })}
            <div></div>
          </div>
          <div className="right-tab-div" style={{ display: !tabDisplay ? "block" : "none" }}>
            <div></div>
            {news.recommend.map((item, index) => {
              return (
                <div key={index}>
                  <div>{item[0].trim()}</div>
                  <div>{item[1].trim()}</div>
                </div>
              )
            })}
            <div></div>
          </div>
          <div className="box_footer" ></div>
        </div>
      </div>
    </div>
  );
};

RightLayout.propTypes = {};
RightLayout.defaultProps = {};
