import React, { useState, useEffect } from 'react';
import centerImg from '../images/center-content.svg';
// import PropTypes from 'prop-types';

import SvgCom from './svg';

export default function CenterLayout() {
  const [nowTime, setNowTime] = useState('00时00分00秒');
  const [nowDate, setNowDate] = useState('2020年01月01日');
  useEffect(() => {
    const changeTime = () => {
      const date = new Date();
      const year = date.getFullYear();
      const month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const hour = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
      const minute = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
      const second = date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds();
      setNowDate(`${year}年${month}月${day}日`);
      setNowTime(`${hour}时${minute}分${second}秒`);
    };
    const intervalId = setInterval(() => {
      changeTime();
    }, 1000);
    return () => {
      clearInterval(intervalId);
    };
  }, [setNowTime, setNowDate]);

  return (
    <div className="center ">
      {/* <div className="boxContainer ">
        <div className="box ">

          <div className="box_footer" />
        </div>
      </div> */}
      <div>
        <div id="time">{nowDate}</div>
        <div>上海城市运行“一网统管”金融指数</div>
        <div>{nowTime}</div>
      </div>
      <div>
        <div>
          {/* <img src={centerImg}  /> */}
          <SvgCom width={'100%'} height={'100%'} />
          {/* <object id="mySvg" data={centerImg} type="image/svg+xml">
            加载失败
          </object>  */}
        </div>
      </div>
    </div>
  );
}

CenterLayout.propTypes = {};
CenterLayout.defaultProps = {};
