import React from 'react';

const StockTable = (props) => {
return (
  <table className="stock-table">
    <thead>
      <tr>
        <th className="left-table-firstlast-column"></th>
        <th className="left-table-second-column">名称</th>
        <th>现价</th>
        <th>涨跌</th>
        <th>涨跌幅</th>
        <th>年初至今</th>
        <th className="left-table-firstlast-column"></th>
      </tr>
    </thead>
    <tbody>
      {props.domestic.map((item) => {
        return (
          <tr key={item[0].trim()}>
            <td className="left-table-firstlast-column"></td>
            <td className="left-table-second-column">{item[0].trim()}</td>
            <td className={item[2] < 0 ? "green-font" : ""}>{item[1].trim()}</td>
            <td className={item[2] < 0 ? "green-font" : ""}>{item[2]}</td>
            <td className={item[2] < 0 ? "green-font" : ""}>{`${item[3]}%`}</td>
            <td className={item[4] < 0 ? "green-font" : ""}>{`${item[4]}%`}</td>
            <td className="left-table-firstlast-column"></td>
          </tr>
        )
      })}
    </tbody>
  </table>
)
}

export default StockTable;